.item-card {
    background: #374151;
    border-radius: 10px;
    overflow: hidden;
    transition: transform 0.3s;
    cursor: pointer;

}
.item-card:hover {
    transform: scale(1.05);
}
.item-image {
    aspect-ratio: 3/4;
    background: linear-gradient(45deg, #3b82f6, #8b5cf6);
    position: relative;
    overflow: hidden;
}
.item-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.item-info {
    padding: 15px;
}
.item-title {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.item-meta {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
}
.item-meta .chapter {
    color: #fb923c;
}
.item-meta .time {
    color: #9ca3af;
}